.appWrapp {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 100%;
  padding: 1rem;
  box-sizing: border-box;
}

.downloadBtnWrap {
  box-sizing: border-box;
  margin: 1rem auto;
}

.btn {
  border: 1px solid;
  border-radius: 0.25rem;
  cursor: pointer;
  outline: 0;
  background-color: transparent;
  padding: 0.5rem 1rem;
}
