.wrapRequestForm {
  display: flex;
  flex-direction: column;
  max-width: 90%;
}

.requestFormField {
  width: 100%;
  min-height: 3.25rem;
  color: #00698c;
  outline: 0;
  box-sizing: border-box;
  padding: 0.75rem 0.75rem 0 0.75rem;
  border: 1px solid #8a8a89;
  border-radius: 5px;
  font-size: inherit;
}

.requestFormField:focus {
  border: 1px solid #227e9e;
  color: #00698c;
}

.requestFormLabel {
  position: relative;
  top: -2.162rem;
  color: #8a8a89;
  pointer-events: none;
  padding: 0 0.75rem;
  transition: top 350ms;
  -webkit-transition: top 350ms;
}

.requestFormField:focus + .requestFormLabel {
  color: #00698c;
  font-size: small;
  top: -2.962rem;
  margin-bottom: 2px;
}

.activeLabel {
  color: #00698c;
  font-size: small;
  top: -2.962rem;
  margin-bottom: 2px;
}

.activeInputField {
  border: 1px solid #227e9e;
}

